<template>
  <div>
    <!-- Headers -->
    <MyHeader @changeComponents="changeComponents"></MyHeader>
    <!-- contents -->
    <MyAbroad v-if="tabIndex === 1"></MyAbroad>
    <MyOffer v-else></MyOffer>
    <!-- Footer -->
    <HomeFooter />
  </div>
</template>

<script>
import MyHeader from "@/components/MyHeader";
import HomeFooter from "@/components/HomeFooter";
import MyAbroad from "@/components/index/Abroad";
import MyOffer from "@/components/index/Offer";

export default {
  name: "MyHome",
  components: {
    MyHeader,
    HomeFooter,
    MyAbroad,
    MyOffer,
  },
  data() {
    return {
      tabIndex: 1,
    };
  },
  methods: {
    changeComponents(index) {
      this.tabIndex = index;
    },
  },
};
</script>

